
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Reem Kufi", sans-serif !important;
}

img {
  max-width: 100%;
  height: auto;
}

.nutrition-section {
  background-color: #000;
  color: #fff;
  padding: 20px;
  text-align: center;
  background-image:url(Assets/wheat.png);
  background-position: -20rem 0rem;
  background-repeat: no-repeat;
  background-size: cover;
    
}
.Exercise-section{
  background-color: #000;
  color: #fff;
  padding: 20px;
  text-align: center;
}

.image-slider {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5rem; /* Add margin for spacing between sliders */
  padding-top: 5rem;
  z-index: 1;
}

button {
  background-color: #333;
  font-size: 24px;
  padding: 12px;
  cursor: pointer;
}

img {
  max-width: 100%;
  max-height: 300px;
  margin: 0 10px;
  display: block;
}

.App {
  min-height: 100vh;
  width: 100%;
  max-width: 1900px;
  margin: 0rem auto;
  background-image: url(Assets/amoledbackground.jpg);
  
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: black;
  padding: 20px;
  border-radius: 8px;
  max-width: 400px;
  text-align: center;
}

.modal button {
  background-color: #333;
  color: #fff;
  font-size: 16px;
  padding: 8px 16px;
  cursor: pointer;
  margin-top: 10px;
}
body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  background-color: #f6f6f6;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 90px;
  position:fixed; /*esto para que se quede fija cuando scrolleamos, que aparezca siempre*/
  width: 100%;   
  z-index: 1; /*z index es para hacer q este x encima de todo*/
  background: orange;
  border-radius:5rem;
  opacity: 90%;
}

.navbar-menu-container {
  display: none;
}
.navbar-links-container a {
  margin-right: 3rem;
  text-decoration: none;
  color: black;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 0.5rem;
  background-color: orange;
}
.navbar-links-container a:hover {
  margin-right: 3rem;
  text-decoration: none;
  color: rgb(99, 4, 241);
  font-size: 1.1rem;
  font-weight: 600;
}
.primary-button {
  padding: 0.9rem 1.75rem;
  background-color: white;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  transition: 0.2s;
  margin-right: 1rem;
}
.navbar-cart-icon {
  font-size: 1.15rem;
}
body.popup-open {
  overflow: hidden; /* Prevent scrolling when the popup is open */
}
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); 
  backdrop-filter: blur(5px); 
  z-index: 1;
}
.popupShop{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  width: 65rem; /* Set the desired width */
  height: 55rem; /* Set the desired height */
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2); /* 8px 16px in rem */
  z-index: 2;
  text-align: center;
  overflow: auto; /* Add scroll if needed */
}
.popupShopInner{
  text-align: center;
}
.popupOptionContainer {
  position: relative;
  justify-content: space-between; /* Adjusted spacing */
  align-items: flex-start; /* Align items at the top */
  margin-top: 2rem; /* Adjusted margin */
  width: 100%; /* Make the container full width */
}
.popupOption {
  display: inline-block; /* Display boxes in a line */
  margin: 0 1rem; /* Adjusted margin */
  cursor: pointer;
  background: orange;
  border: 1px solid #ccc;
  border-radius: 0.3125rem; /* 5px in rem */
  padding: 19.5rem 3rem; /* Adjusted padding */
}

.popupOption h3 {
  margin-bottom: 1rem;
}

.popupOption.selected {
  background: #e6f7ff; /* Change the background color for the selected option */
}
.confirmButton {
  background: #4caf50;
  margin-top: 2rem;
  color: #fff;
  padding: 0.75rem 1.5rem; /* 12px 24px in rem */
  border: none;
  border-radius: 0.3125rem; /* 5px in rem */
  cursor: pointer;
}
.popupShopInner .closeShop{
  position: absolute;
  top: 1rem; /* 16px in rem */
  right: 1rem; /* 16px in rem */
  cursor: pointer;
}

.primary-button:hover {
  background-color: rgb(234, 234, 234);
}
.navbar-menu-container svg {
  font-size: 1.5rem;
  cursor: pointer;
}
.home-banner-container {
  position: relative;
  display: flex;
  padding-top: 3rem;
}
.home-bannerImage-container {
  position: absolute;
  top: -100px;
  right: -170px;
  z-index: -2;
  max-width: 700px;
}
.home-image-section {
  max-width: 600px;
  flex: 1;
  padding: 3rem;
}
.home-text-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-left: 2rem;
}
.primary-heading {
  font-size: clamp(2rem, 5vw, 4rem);
  color: white;
  /* line-height: 5rem; */
  max-width: 600px;
  padding-top: 5rem;
}
.primary-text {
  font-size: clamp(1rem, 3vw, 1.5rem);
  max-width: 500px;
  color: white;
  margin: 1.5rem 0rem;
}
.secondary-button {
  padding: 1rem 2.5rem;
  background-color: #e48f0f;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  color: white;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 25rem;
}
.secondary-button svg {
  margin-left: 0.75rem;
  font-size: 1.5rem;
}
.secondary-button:hover {
  background-color: #764803b9;
}
.about-section-container {
  margin-top: 5rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 5rem;
}
.about-background-image-container {
  position: absolute;
  left: -150px;
  z-index: -2;
  padding-left: 5rem;
}
.about-section-image-container {
  flex: 0.9;
  margin-right: 1rem;
}
.about-section-text-container {
  flex: 1;
  justify-content: center;
  display: flex;
  flex-direction: column;
  padding-right: 30rem;
}
.primary-subheading {
  font-weight: 700;
  color: #fe9e0d;
  font-size: 1.15rem;
}
.about-buttons-container {
  margin-top: 2rem;
  display: flex;
}
.watch-video-button {
  margin-left: 2rem;
  background-color: transparent;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  color: white;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.watch-video-button svg {
  font-size: 3rem;
  margin-right: 1rem;
}
.contact-page-wrapper h1 {
  max-width: 900px !important;
}
.contact-page-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 6rem 0rem;
}
.contact-form-container {
  background-color: white;
  max-width: 700px;
  width: 100%;
  margin-top: 3rem;
  display: flex;
  align-items: center;
  padding: 1rem;
  border-radius: 5rem;
}
.contact-form-container input {
  flex: 1;
  height: 100%;
  border: none;
  outline: none;
  font-size: 1.3rem;
  padding: 0.5rem 1rem;
}
.footer-wrapper {
  margin: 0.5rem 0rem;
  display: flex;
  margin-top: 10rem;
}
.footer-logo-container {
  max-width: 20rem;
  background-color: black;
  border-radius: 2rem;

}
.footer-icons {
  margin-top: 2.5rem;
}
.footer-icons svg {
  font-size: 1.5rem;
  margin-right: 1.25rem;
  color: #585858;
}
 .footer-section-one {
 background-color: orange;
} 
.footer-section-two {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  background-color: orange;
}
.footer-section-columns {
  display: flex;
  flex-direction: column;
  min-width: 190px;
  
}
.footer-section-columns span {
  margin: 0.25rem 0rem;
  font-weight: 600;
  color: #4c4c4c;
  cursor: pointer;
  
}
.testimonial-section-bottom {
  margin-top: 2rem;
  background-color: white;
  padding: 1.5rem 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 750px;
  border-radius: 2rem;
  flex-direction: column;
  text-align: center;
}
.testimonial-section-bottom {
  margin: 2rem auto;
}
.testimonial-section-bottom p {
  font-weight: 700;
  color: #515151;
  max-width: 500px;
  font-size: 1.1rem;
  margin: 2rem 0rem;
}
.testimonials-stars-container svg {
  margin: 0rem 0.25rem;
  font-size: 1.5rem;
  color: #fe9e0d;
}
.testimonial-section-bottom h2 {
  margin-top: 1.5rem;
}
.work-section-wrapper {
  margin-top: 15rem;
}
.work-section-top p {
  text-align: center;
  max-width: 600px !important;
}
.work-section-top h1 {
  max-width: 700px !important;
}
.work-section-top {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.work-section-info {
  width: 290px;
  min-height: 350px;
  background-color: white;
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 1rem;
  color: #505050;
  margin: 1rem 2rem;
  cursor: pointer;
}
.work-section-info:hover{
  width: 290px;
  min-height: 350px;
  background-color: rgb(15, 15, 15);
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 4rem;
  border-color: rgb(255, 162, 0);
  border-style:groove;
  color: #fdfdfd;
  margin: 1rem 2rem;
}
.work-section-info h2 {
  margin: 1rem 0rem;
}
.work-section-bottom {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.work-section-info p {
  flex: 1;
  display: flex;
  align-items: center;
  font-weight: 600;
}

@media (max-width: 1000px) {
  .navbar-links-container a {
    margin-right: 1rem;
    font-size: 1rem;
  }
  .primary-button {
    font-size: 1rem;
  }
  .home-bannerImage-container {
    max-width: 600px;
  }
}
@media (max-width: 800px) {
  .nav-logo-container {
    max-width: 140px;
  }
  .navbar-links-container {
    display: none;
  }
  .navbar-menu-container {
    display: flex;
  }
  .home-bannerImage-container {
    max-width: 450px;
  }
  .home-banner-container,
  .about-section-container {
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
  .about-section-container {
    flex-direction: column;
  }
  
  .home-image-section,
  .about-section-image-container {
    width: 100%;
    max-width: 400px;
    
  }
  .primary-heading {
    text-align: center;
    max-width: 90%;
  }
  .primary-text {
    text-align: center;
    max-width: 80%;
  }
  .home-text-section,
  .about-section-text-container {
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
  }
  .secondary-button {
    font-size: 1rem;
    padding: 0.8rem 2rem;
    cursor: pointer;
  }
  .about-section-container {
    margin-top: 5rem !important;
  }
  .about-buttons-container {
    justify-content: center;
    flex-direction: column;
  }
  .primary-subheading {
    text-align: center;
  }
  .watch-video-button {
    margin-left: 0rem !important;
    margin-top: 1rem;
    font-size: 1rem !important;
  }
  .watch-video-button svg {
    margin-right: 0.5rem !important;
  }
  .about-section-image-container {
    margin-right: 0rem !important;
  }
  .work-section-wrapper {
    margin-top: 5rem !important;
  }
  .work-section-bottom {
    margin-top: 1rem !important;
  }
  .contact-form-container {
    padding: 0.5rem !important;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 1rem !important;
  }
  .contact-form-container input {
    font-size: 1rem !important;
    max-width: 100%;
    padding: 0.5rem 1rem !important;
    margin-bottom: 0.8rem !important;
    text-align: center;
  }
  .footer-wrapper {
    flex-direction: column;
  }
  .footer-section-two {
    flex-direction: column;
    margin-top: 2rem;
  }
  .footer-section-columns {
    margin: 1rem 0rem;
  }
  .App {
    max-width: 95vw;
  }
}
@media (max-width: 600px) {
  .home-bannerImage-container,
  .about-background-image-container {
    display: none;
  }
}

.image-slider-button{
  cursor: pointer;
  width: 5rem;
}
.add-button{
  margin-left: 56rem ;
  width: 4rem;
  cursor: pointer;
  padding-top: 1rem;
}
.add-button-popup{
  margin-left: 9.3rem ;
  width: 4rem;
  cursor: pointer;
  
}
.backButton{
  padding-top: 1rem;
  margin-left: 53rem;
  cursor: pointer;
}
.backButtonPopup{
  padding-top: 1rem;
  margin-left: 6rem;
  cursor: pointer;
}
.nutrition-text{
  margin-left: 42rem;
  padding-bottom: 2rem;
}
